import React, { useEffect, useState } from 'react';
import { createTheme, ThemeProvider } from '@mui/material/styles';

import { errorMsg, getToken } from 'src/config/helper';
import { get_photos } from 'src/config/WebServices';
import { getResource } from 'src/config/SimpleApis';

import FilerobotImageEditor, {
  TABS,
  TOOLS,
} from 'react-filerobot-image-editor';
import { Helmet } from 'react-helmet-async';
import CssBaseline from '@mui/material/CssBaseline';
import PageTitleWrapper from 'src/components/PageTitleWrapper';
import PageSubHeader from 'src/content/pages/Components/SubHeader';
import Container from '@mui/material/Container';


const theme = createTheme();

export default function Album() {
  const [loading, setLoading] = useState(false);
  const [allImages, setAllImages] = useState([]);
  const [isImgEditorShown, setIsImgEditorShown] = useState(false);

  const openImgEditor = () => {
    setIsImgEditorShown(true);
  };

  const closeImgEditor = () => {
    setIsImgEditorShown(false);
  };

  useEffect(() => {
    handleGetPhotos();
  }, []);

  const handleGetPhotos = async () => {
    try {
      setLoading(true);
      const token = getToken();
      const res = await getResource(get_photos, token);
      setAllImages(res);
      console.log("get photos",res)
    } catch (e) {
      errorMsg(e);
    } finally {
      setLoading(false);
    }
  };

  return (
    <>

      <Helmet>
        <title>GFP - Home</title>
      </Helmet>
      <CssBaseline />
      {/* End hero unit */}
      <PageTitleWrapper>
      </PageTitleWrapper>

      <Container sx={{ py: 1 }} maxWidth="md">
        <FilerobotImageEditor
          source="https://source.unsplash.com/random"
          annotationsCommon={{
            fill: '#ff0000',
          }}
          onSave={(editedImageObject, designState) =>
            console.log('saved', editedImageObject, designState)
          }
          onClose={closeImgEditor}
          Text={{ text: 'Filerobot...' }}
          Rotate={{ angle: 90, componentType: 'slider' }}
          Crop={{
            presetsItems: [
              {
                titleKey: 'classicTv',
                descriptionKey: '4:3',
                ratio: "4 / 3",
              },
              {
                titleKey: 'cinemascope',
                descriptionKey: '21:9',
                ratio: "21 / 9",
              },
            ],
            presetsFolders: [
              {
                titleKey: 'socialMedia',
                groups: [
                  {
                    titleKey: 'facebook',
                    items: [
                      {
                        titleKey: 'profile',
                        width: 180,
                        height: 180,
                        descriptionKey: 'fbProfileSize',
                      },
                      {
                        titleKey: 'coverPhoto',
                        width: 820,
                        height: 312,
                        descriptionKey: 'fbCoverPhotoSize',
                      },
                    ],
                  },
                ],
              },
            ],
          }}
          tabsIds={[TABS.ADJUST, TABS.ANNOTATE, TABS.FILTERS]} // or {['Adjust', 'Annotate', 'Watermark']}
          defaultTabId={TABS.ANNOTATE} // or 'Annotate'
          defaultToolId={TOOLS.TEXT} // or 'Text'
          savingPixelRatio={0} previewPixelRatio={0} />
      </Container>
    </>
  );
}
